#topbar{
	background-color: #FFFFFF;
	position: absolute;
	margin-top: 0px;
	width:100%;
	height: 64px;
    -webkit-box-shadow:0px 1px 1px #555459;
    box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2);
}


#logo{
    position: relative;
    display: inline-block;
	background-image: url("./../prepleaf.jpg");
	background-size : 100% auto;
    background-repeat: no-repeat;
    height:48px;
    width:48px;
    margin-left:20px;
    margin-top:8px;
}

#notification-wrapper{
    position: relative;
    display: inline-block;
    height: 100%;
    width: 200px;
    margin-right: 85px;
    float:right;
}


