#question_modal {
  position: relative;
  width: 600px;
  top: 150px;
  left: 0;
  right: 0;
  margin: 0 auto;
  overflow: auto;
}

#que_input {
	position: relative;
	left: 50px;
}

#title{
	position: relative;
	left: 50px;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

textarea {
    border: none;
    border-bottom: 1px solid;
    overflow: auto;
    outline: none;

    -webkit-box-shadow: none;
    box-shadow: none;
}

#cancel{
    position:relative;
    right: 40px;
    bottom: 7px;
}


#header{
  border: 0px;
}

#footer{
  height: 50px;
  background-color:#F5F5F5;
  overflow: auto;
}

#askbutton{
  position: relative;
  bottom: 7px;
}

#scroll{
  position:absolute;
  right: 40px;
  top: 27px;
  z-index: 12;
}

#options{
  position:relative;
}

#categories{
  position: absolute;
  top: 12px;
  right: 35px;
}













