#notifications{
	position: relative;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	width: 280px;
	-ms-flex-align: center;
	    align-items: center;
}

#notification-list{
	position: absolute;
	display: none;
    top: 64px;
    background-color: #888888;
    border-radius: 4px;
    z-index: 10;
}

.first-notification{
	position: relative;
	padding: 3px 5px;
}

.last-notification{
	border-top: 1px solid #000000;
	padding: 3px 5px;
}


