#askquestion{
	position: relative;
	top: 100px;
	left: 85%;
	width: 130px;
	height: 40px;
	font-size: 16px;
	border: 1px solid;
	border-radius: 5px;
	text-align: center;
	padding-top: 8px;
	border-color: #962520;
	background-color: #b92b27;
	color: #ffffff;
}

#askquestion:hover{
	position: relative;
	top: 100px;
	left: 85%;
	width: 130px;
	height: 40px;
	font-size: 16px;
	border: 1px solid;
	border-radius: 5px;
	text-align: center;
	padding-top: 8px;
	
	background-color: #c92b27;
	border-color:#962520;
}


