#category {
    position: relative;
    top: 163px;
    width: 200px;
    left: 83%;
    z-index: 5;
    margin-left: 10px;
    background-color:#FFFFFF;
}

#category_title {
	font-size: 20px;
	color: #0000FF;
    border-bottom: 2px solid #F5F5F5;
}

.nav>.category_i>a{/*change here*/
    border-radius: 3px;
}

.nav>.category_i.active>a{/*change here*/
    background-color:#337ab7;
    border-radius: 3px;
}

.nav>.category_i.active:hover>a{/*change here*/
    background-color:#337ab7;
    border-radius: 3px;
}

.nav>.category_i>a:hover{
    color:#FF0000;
    background-color:#FFFFFF;
    border-radius: 0px;
    border-color:#FFFFFF;
}

.nav>.category_i>a:hover{
    color:#FF0000;
    background-color:#FFFFFF;
    border-radius: 0px;
    border-color:#FFFFFF;
}

