.answer_i{
	position: relative;
	left: 25px;
	/*width: 90%;*/
	/*margin: 0 auto;*/
	/*background-color: #ffffff;*/
	margin-top: 10px;
	margin-bottom: 10px;
	border-radius: 3px;
	padding: 5px 10px;
}

.answer-option-container {
	position: relative;
	display: inline-block;
	vertical-align: top;
	z-index: 10;
}

.answer-option:hover {
	position: absolute;
	top: 1px;
	width: 16px;
	height: 9px;
	opacity: 0.5;
	background-image: url("dots.png");
	background-repeat: no-repeat;
	background-size: 100%;
	background-position: 0px -4px;
}

.answer-option {
	position: absolute;
	top: 1px;
	width: 16px;
	height: 9px;
	opacity: 0;
	background-image: url("dots.png");
	background-repeat: no-repeat;
	background-size: 100%;
	background-position: 0px -4px;
}

.answer-edit {
	position: absolute;
	display: none;
	top: 14px;
	width: 70px;
	padding: 0px 10px;
	background-color: #ffffff;
	border-radius: 4px 4px 0px 0px;
}

.answer-delete {
	position: absolute;
	display: none;
	top: 35px;
	width: 70px;
	padding: 0px 10px;
	background-color: #ffffff;
	border-radius: 0px 0px 4px 4px;
	border-top: 1px solid #000000;
}

.answer-pic{
	position: relative;
	display: inline-block;
	vertical-align: top;
	width: 24px;
	height: 24px;
	background-image: url("user.png");
}

.answer-content-i{
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0px 5px;
	padding: 0px 5px;
    border-radius: 5px;
	background-color: #ffffff;
}


