.question{
	font-size: 18px;
	font-weight: bold;
	color: rgb(45, 45, 45);
}

.list-group-item:first-child{
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
}

.question_content{
	margin-top: 10px;
	margin-left: 10px;
}

.que_no{
	font-size: 15px;
	color: rgb(145, 145, 145);
	font-weight: normal;
}

.options{
	font-size: 15px;
	font-weight: normal;
	color: rgb(145, 145, 145);
}

.answer_question{
	width: 150px;
	height: 42px;
}

.pencil{
	display: inline-block;
	background-image: url(./pencil.jpg);
	background-size: 20px 20px;
	background-repeat: no-repeat;
	width: 20px;
	height: 20px;
}

.zzz {
	display: inline-block;
	position: relative;
	top: -5px;
	margin-left: 6px;
}

#upvotes{
	background-color: #f1f8fc;
	position: relative;
	top: 8px;
	width:60px;
	height:25px;
	padding: 2px 6px;
	border: 1px solid;
	border-radius: 3px;
}

.upvotes{
	position: relative;
	display: inline-block;
	margin: 0px 10px;
	height: 28px;
	border: 1px solid #000000;
	border-radius: 4px;
}

.published-on{
	position: relative;
	display: inline-block;
	margin: 0px 10px;
}

.answers{
	position: relative;
	display: inline-block;
	margin: 0px 10px;
}

.views{
	position: relative;
	display: inline-block;
	margin: 0px 10px;
}

.upvote-text{
	position: relative;
	display: inline-block;
	top: 2px;
	height: 20px;
	padding: 0px 5px;
}

.upvote-line{
	position: relative;
	display: inline-block;
	top: 5px;
	width: 0px;
	border-left: 1px solid #000000;
	height: 16px;
}

.upvote-number{
	position: relative;
	display: inline-block;
	height: 20px;
	top: 2px;
	padding: 0px 5px;
}


